import React from 'react';
import './styles.css';
import {AppConsumer} from '../../context';
import {BASE_URL} from '../../utils/api';

class AudioPlayer extends React.Component{

    constructor(props){
        super(props);
    }

    componentDidMount(){
        this.player.addEventListener("playing", () => this.props.resume())
        this.player.addEventListener("pause", () => this.props.pause())
        this.player.addEventListener("seeking", () => console.log("this.props.pause()"))
    }


   
    componentDidUpdate(previousProps, previousState){ 
        console.log("updates...");        
        
        if(this.props.audioState.playState == "playing"){
            console.log("this.player.play");
            
            this.player.play();
        }else if(this.props.audioState.playState == "paused"){
            console.log("this.player.pause");
            this.player.pause();
            
        }
        else{
            console.log("stopped");
            
            return;            
        }
    }  
        
                    
    

    render(){

        return <section id="audio-player">
                                  <div className="audio-details">
                                      <h4>
                                          {this.props.audioState.nowPlaying.podcast.title} - {this.props.audioState.nowPlaying.title}
                                      </h4>
                                  </div>
                                  <audio
                                    ref={ref => this.player = ref}
                                    controls
                                    preload="none"
                                    src={`${BASE_URL}/${this.props.audioState.nowPlaying.path}`}>
                                        Your browser does not support the
                                        <code>audio</code> element.
                                  </audio>
                                </section> 
    }
} 
    
    export default AudioPlayer;