import React from 'react';
import './styles.css';
import {AppConsumer} from '../../context';   

export default function PlaylistTable({episodes, podcast}) {
    console.log(episodes);
    
    
    let table_rows = episodes
    ?episodes.map(
        (episode, index) => <tr key={index} ><td>Hit the lights</td><td>Metallica</td><td>Kill 'em all</td></tr>)
    : "loading..."

    return (
        <AppConsumer>
        {    
            state => <div>
                        <table>
                            <tbody>
                                {
                                    episodes
                                    ?episodes.map(
                                        (episode, index) => <tr key={index} onClick={() => state.playTrack(episode)}><td>{episode.title}</td><td>BBC</td><td>{podcast}</td></tr>)
                                    : "loading..."
                                }
                            </tbody>
                        </table>            
                    </div>
        }
        </AppConsumer>
    )
}
